<template>
    <div id="Button" class="layout">
      <p class="tit">Button按钮</p>
      <div class="cancelFloat">
            <Button
              :butContent="'灰色'"
              :type="'info'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'绿色'"
              :type="'success'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'黄色'"
              :type="'warning'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'默认'"
              :float="'l'"
              @click="butfn"
            />
            <Button
              :butContent="'红色'"
              :type="'error'"
              :float="'l'"
              @click="butfn"
            />
      </div>
      <!-- 以下是参数说明 -->
      <p class="tit">Button属性</p>
      <Formtable :contentData="contentData" />
      <p class="tit">Button事件</p>
      <Formtable 
        :titData ="titDataEvent"
        :contentData="contentDataEvent" 
      />
    </div>
    
</template>

<script>
import { getCurrentInstance } from "vue";
export default {
  setup(props) {
    const { proxy } = getCurrentInstance();
    const contentData = [
      ["Width", "按键宽度", "String", "px", "auto"],
      ["BGcolor", "按键背景颜色", "String", "——", "#00a2ff"],
      ["FontColor", "字体颜色", "String", "——", "#fff"],
      ["fontSize", "字体大小", "Number", "——", "16"],
      ["lineHeight", "内容行高", "Number", "——", "30"],
      ["butContent", "按键内容", "String", "——", "确认"],
      ["type", "按键类型", "String", "info、success、warning、error、default", "default"],
      ["float", "按键是否浮动", "String", "l、r", "——" ],
    ];
    const titDataEvent = [ "事件名", "说明", "参数"]
    const contentDataEvent = [["click", "点击触发事件", "type 按键类型"]]
    const butfn =function (type){
      // 自定义弹框
      proxy.h_tips({
          type:type,
          msg: type+ " 类型",
          top:30,
          time:1000
      })
    }
    return{
      contentData,
      titDataEvent,
      contentDataEvent,
      butfn
    }
  }
}
</script>

